<template>
    <PageHeader :title="t(`pages.releases.title`)"
        :detail="!!data.selectedReleases?.length"
        id="item-head-buttons"
        v-show="!data.releaseEditorMode && !data.trackEditorMode && !data.licenseTrackEditorMode"
    />

    <div class="box" v-show="!data.releaseEditorMode && !data.trackEditorMode && !data.licenseTrackEditorMode">
        <div class="item-detail">
            <Releases
                :canAdd="false"
                @select="onSelectReleases"
                @add="addReleases"
                @edit="editReleases"
                @editTracks="editReleaseTracks"
                @editTracksLicenses="editReleaseTracksLicenses"
                ref="releases"
            />
        </div>
    </div>

    <!-- Ð ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ñ€ÐµÐ»Ð¸Ð·Ð¾Ð² -->
    <ReleaseEditor v-if="data.releaseEditorMode"
        :items="data.editorReleases"
        ref="releaseEditor"
        @remove="onRemoveRelease"
        @update="onUpdateRelease"
        @add="onAddRelease"
        @close="closeReleaseEditorMode"
    />

    <!-- Ð ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ñ‚Ñ€ÐµÐºÐ¾Ð² -->
    <TrackEditor v-if="data.trackEditorMode"
        :release="data.editorReleases?.length ? data.editorReleases[0] : null"
        :items="data.editorTracks"
        ref="trackEditor"
        @remove=""
        @update=""
        @add=""
        @close="closeTrackEditorMode"
    />

    <!-- Ð ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ð»Ð¸Ñ†ÐµÐ½Ð·Ð¸Ð¹ Ñ‚Ñ€ÐµÐºÐ¾Ð² -->
    <LicenseTrackEditor v-if="data.licenseTrackEditorMode"
        :artist="data.item"
        :release="data.editorReleases?.length ? data.editorReleases[0] : null"
        :items="data.editorTracks"
        ref="licenseTrackEditor"
        @close="closeLicenseTrackEditorMode"
    />
</template>

<script setup>
    import { reactive, computed, ref } from 'vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';

    import Releases from '@/components/blocks/Releases.vue';
    import ReleaseEditor from '@/components/blocks/ReleaseEditor/ReleaseEditor.vue';
    import TrackEditor from '@/components/blocks/TrackEditor/TrackEditor.vue';
    import LicenseTrackEditor from '@/components/blocks/LicenseTrackEditor/LicenseTrackEditor.vue';
    import api from 'ðŸ”—/api';

    import { useI18n } from 'vue-i18n';

    const { t } = useI18n();
    const releases = ref(null)
    const releaseEditor = ref(null);
    const licenseTrackEditor = ref(null);
    const trackEditor = ref(null);

    const data = reactive({
        releaseEditorMode: false,
        editorReleases: null,
        trackEditorMode: false,
        editorTracks: null,
        selectedReleases: null
    });

    // Releases
    const onSelectReleases = (releases) => {
        data.selectedReleases = releases?.length ? releases : null;
    }

    const addReleases = (count) => {
        data.releaseEditorMode = true;

        // 
        setTimeout(() => {
            releaseEditor.value?.create(count || 1);
        });
    }

    const editReleases = (releases) => {
        data.editorReleases = releases;
        data.releaseEditorMode = true;
    }

    const editReleaseTracks = async (release) => {
        const payload = await api.releases().tracks.list(release.id);

        data.editorTracks = payload?.items || [];
        data.editorReleases = [...(data.selectedReleases || [])];
        data.trackEditorMode = true;
    }

    const editReleaseTracksLicenses = async (release) => {
        const payload = await api.releases().tracks.list(release.id);

        data.editorTracks = payload?.items || [];
        data.editorReleases = [...(data.selectedReleases || [])];
        data.licenseTrackEditorMode = true;
    }

    const closeReleaseEditorMode = () => {
        data.releaseEditorMode = false;
    }
        // Events
        const onAddRelease = (release) => {
            if (release?.id) {
                releases.value.addItem(release);
            }
        }

        const onRemoveRelease = (id) => {
            if (id) {
                releases.value.removeItems([{ id }], true)
            }
        }

        const onUpdateRelease = (release) => {
            if (release?.id) {
                releases.value.updateItem(release);
            }
        }

    // Tracks
        const closeTrackEditorMode = () => {
            data.trackEditorMode = false;
        }

        const closeLicenseTrackEditorMode = () => {
            data.licenseTrackEditorMode = false;
        }
</script>

<style lang="scss" scoped>
    @include studio.default-box;
</style>
